<template>
  <div>
    <div class="module">
      <el-table :data="tableData" style="width: 100%" @row-click="openDetails">

        <el-table-column prop="id" label="ID" width="100" align="center"> </el-table-column>

        <el-table-column prop="name" label="模板名">
        </el-table-column>

        <el-table-column label="更多" fixed="right" width="125">
          <template slot-scope="scope">
            <el-dropdown>
              <el-button type="primary">
                更多<i class="el-icon-arrow-down el-icon--right"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item @click.native="change(scope.row.id)"
                  >编辑信息</el-dropdown-item
                >
                <el-dropdown-item @click.native="manager(scope.row.id, 1)"
                  >删除</el-dropdown-item
                >
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>

      </el-table>
    </div>

    <div class="module">
      <Pagination
        @pagination="getList()"
        :page.sync="currentPage"
        :pageCount.sync="totalPage"
        :total.sync="total"
        v-if="total > 0"
      />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { Categorylist, Categorymanager, Categoryadd } from "@/api/paramtemplate";
import Pagination from "@/components/Pagination.vue";
export default {
  name: "Home",
  data() {
    return {
      tableData: [],
      currentPage: 1, // 当前页码
      total: 0, // 总条目
      totalPage: 0, // 总页数
    };
  },
  components: {
    Pagination,
  },
  methods: {
    openDetails (row) {
      this.$router.push({name: "paramtemplateItemlist", query:{
        categoryid:row.id, templatename: row.name
      }});
        //具体操作        
    },
    getList() {
      this.$http(
        Categorylist({
          querypage: this.currentPage,
        }),
        (res) => {
          console.log(res);
          if (res.code == 200) {
            this.$message({
              message: res.msg,
              type: "success",
            });
            this.total = res.data.total;
            this.currentPage = res.data.currentPage;
            this.totalPage = res.data.totalPages;
            this.tableData = res.data.result;
          } else {
            this.$message({
              message: res.msg,
              type: "error",
              duration: 5 * 1000,
            });
          }
        }
      );
    },
    change(id){
      this.$prompt("更改模板名", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
      })
        .then(({ value }) => {
          this.changeapi(id, value)
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消更改",
          });
        });
    },
    manager(id, set) {
      this.$http(Categorymanager({
        id: id,
        set: set
      }), (res) => {
        console.log(res);
        if (res.code == 200) {
          this.getList();
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
    changeapi(id,name) {
      this.$http(Categoryadd({
        id: id,
        name: name
      }), (res) => {
        console.log(res);
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            duration: 5 * 1000,
          });
          this.getList();
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
  },
  created() {
    this.getList();
  },
};
</script>
<style lang="scss" scoped>
</style>